.Login {
  border: 0.5px solid black;
  max-width: 320px;
  float: right;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
@media all and (min-width: 480px) {
    .Login {
      /* padding: 60px 0; */
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: end;
          justify-content: flex-end;
    }
  
    .Login form {
      -ms-flex: 1 1;
          flex: 1 1;
      margin: 0;
      max-width: 320px;
    }

    .Login input {
      border-radius: 5px;
    }
}
@media all and (max-width: 480px) {
    .Login input {
      border-bottom-color: black;
    }
}

.Login ul {
  list-style-type: none;
  -ms-flex-line-pack: end;
      align-content: flex-end;
  padding: 10px;
  margin: 5px;
}

.Login input {
  height: 25px;
  font-size: 14px;
  width: 100%;
}

.Login button {
    width: auto;
    margin: 5px;
    padding: 5px;
    background-color: #03A9F4;
    color: white;
    font-size: 14px;
}


